<template>
	<div>
		<CCard>
			<CCardHeader>
				<b>Edit</b> Stock In Out
			</CCardHeader>
			<CCardBody>
				<CForm class="mt-4">
					<CRow class="mx-auto d-flex justify-content-center">
						<CCol sm="8">
							<CInput
								type="text"
								label="Nama Barang"
								disable
								v-model="request_po.nama_barang"
								horizontal
								placeholder="Masukan Nama Barang"
							/>
							<CInput
								type="text"
								label="Type Barang"
								disable
								v-model="request_po.type"
								horizontal
								placeholder="Masukan Type Barang"
							/>
							<CInput
								type="number"
								label="Quantity"
								v-model="request_po.qty"
								horizontal
								:description="validator.qty_msg"
		                        :is-valid="validator.qty"
		                        @input="barang.qty.length < 1 ? validator.qty = false : validator.qty = true"
		                        autocomplete="qty"
		                        placeholder="Masukan Quantity"
		                    />
		                    <CInput
								type="date"
								label="Tanggal Barang"
								v-model="request_po.tanggal"
								horizontal
								placeholder="Masukan Tanggal Barang"
								:description="validator.tanggal_msg"
		                        :is-valid="validator.tanggal"
							/>
							<CTextarea
								label="Alasan"
								v-model="request_po.alasan"
								horizontak
								placeholder="Masukan alasan"
							/>
						</CCol>
					</CRow>
				</CForm>
			</CCardBody>
			<CCardFooter>
				<CButton type="submit" color="primary" @click.prevent="updateStockInOut">
					{{label}}
				</CButton>
			</CCardFooter>
		</CCard>
	</div>
</template>
<script type="text/javascript">
	export default {
		name: 'EditStockInOut',
		data() {
			return {
				label: 'Update Stock In Out',
				request_po: {
					nama_barang: null,
					type: null,
					qty: null,
					alasan: null
				},
				validator: {
					qty: null,
					qty_msg: null,
					tanggal: null,
					tanggal_msg: null
				},
				errors: []
			}
		},
		methods: {
			getData() {
				let headers = new Headers()
				headers.append('Authorization', 'bearer ' + localStorage.token)
				let options =  {
					method:'POST',
					headers,
					redirect: 'follow'
				}

				fetch('https://young-temple-67589.herokuapp.com/api/stock/' + this.$route.params.id, options)
				.then(res => res.json())
				.then(res => {
					if(res.errcode == 40001) {
						this.$store.dispatch('logout')
		                  .then(() => {
		                    let path = window.location.href
		                    path = path.split('/')
		                    localStorage.setItem('prevPath', path[path.length - 1])
		                    alert('Session Login kamu sudah habis! silahkan login kembali')
		                    
		                  })
		                  .then(() => {
		                    this.$router.replace({path: '/login'})
		                  })
		                  .catch(e => {
		                    alert('An error occured when get data :(')
		                    return false
		                  })
					}
					console.log(res)
					this.request_po = res
				})
				.catch(e => {
					if(e.response.status == 401) {
						this.$store.dispatch('logout')
		                  .then(() => {
		                    let path = window.location.href
		                    path = path.split('/')
		                    localStorage.setItem('prevPath', path[path.length - 1])
		                    alert('Session Login kamu sudah habis! silahkan login kembali')
		                    
		                  })
		                  .then(() => {
		                    this.$router.replace({path: '/login'})
		                  })
		                  .catch(e => {
		                    alert('An error occured when get data :(')
		                    return false
		                  })
					}
					else {
						alert('Maaf tidak bisa mengambil data :(')
						console.error(e)
					}
					return false
				})
			},
			updateData() {
				this.errors = []

				if(!this.request_po.qty) {
					this.validator.qty = false
		            this.validator.qty_msg = 'Harap isi quantity barang'
		            this.errors.push('qty barang kosong')
				}

				if(!this.request_po.tanggal) {
					this.validator.tanggal = false
		            this.validator.tanggal_msg = 'Harap isi harga jual barang'
		            this.errors.push('tanggal barang kosong')
				}

				if(!this.request_po.length) {
					this.label = 'Loading...'
					this.$http.post('https://young-temple-67589.herokuapp.com/api/stock/edit/' + this.$route.params.id, this.request_po, {
						headers: {
							'Authorization': 'bearer ' + localStorage.token
						}
					})
					.then(res => {
						if(res.errcode == 40001) {
							this.$store.dispatch('logout')
			                  .then(() => {
			                    let path = window.location.href
			                    path = path.split('/')
			                    localStorage.setItem('prevPath', path[path.length - 1])
			                    alert('Session Login kamu sudah habis! silahkan login kembali')
			                    
			                  })
			                  .then(() => {
			                    this.$router.replace({path: '/login'})
			                  })
			                  .catch(e => {
			                    alert('An error occured when get data :(')
			                    return false
			                  })
						}
						else {
							alert('Update Data Stock In Out berhasil!')
							this.$router.push('/stock-in-out')
						}
					})
					.catch(e => {
						this.label = 'Update Stock In Out'
						if(e.response.status == 401) {
							this.$store.dispatch('logout')
			                  .then(() => {
			                    let path = window.location.href
			                    path = path.split('/')
			                    localStorage.setItem('prevPath', path[path.length - 1])
			                    alert('Session Login kamu sudah habis! silahkan login kembali')
			                    
			                  })
			                  .then(() => {
			                    this.$router.replace({path: '/login'})
			                  })
			                  .catch(e => {
			                    alert('An error occured when get data :(')
			                    return false
			                  })
						}
						else {
							alert('Maaf, Tidak bisa mengupdate data stock in out')
							return false
						}
					})
				}
				else {
					this.label = 'Update Stock In Out'
					console.log(this.errors)
	         		window.scrollBy({ 
		              top: -500, // could be negative value
		              left: 0, 
		              behavior: 'smooth' 
		            })
		            return false
				}
			}
		},
		created() {
			this.getData()
		}
	}
</script>